"use strict";

var components = {
    floatPanel: {
        show: function (elem) {
            //if ($(window).outerWidth() <= elem.outerWidth() + 38) {
            //    elem.css({'margin-left': 0, left: '50px'});
            //} else {
            //    elem.css({'margin-left': -elem.outerWidth()/2, left: $('body').scrollLeft() + $(window).outerWidth()/2});
            //}

            elem.addClass('active');
            $('.operate-mask').addClass('active');
        },

        hide: function (elem) {
            elem.removeClass('active');
            $('.operate-mask').removeClass('active');
        },

        init: function () {
            var that = this;

            //  prevent click event
            $('body').on('click', '.m-float-panel', function (e) {
                e.stopPropagation();
            });

            //  close panel
            $('.m-float-panel .icon-close').click(function (e) {
                e.stopPropagation();
                that.hide($(this).parents('.m-float-panel'));
            });

            $('body').on('keydown', function (e) {
                if (e.keyCode == 27)
                    that.hide($('.m-float-panel'));
            });

            $('.operate-mask').click(function () {
                that.hide($('.m-float-panel'));
            });
        }
    },

    toggle: function (btn, wrap) {
        $(btn).click(function () {
            $(this).toggleClass('active');
            $(this).parents(wrap).toggleClass('active');
        });
    },

    init: function () {
        this.floatPanel.init();
    }
};

$(function (){
    // init app
    components.init();
});


