"use strict";

var calendar = {
    toggleTask: {
        previewJPG: {
            init: function () {
                var that = this;
                var isBind = false;

                //  show psd preview
                $('.calendar .task .icon-jpg').click(function (e) {
                    e.stopPropagation();
                    var iconJpg = that.currentPreviewJpg = $(this);
                    $('.task-preview img').prop('src', iconJpg.attr('data-url'));
                    components.floatPanel.show($('.task-preview'));

                    //  add mouse wheel for slide previews
                    if (!isBind) {
                        isBind = true;
                        $('body').on('mousewheel', previewSlide);
                    }
                });

                //  freeze mousewheel
                function previewSlide (e) {
                    $(this).off('mousewheel', previewSlide);
                    setTimeout(function () {
                        $(this).on('mousewheel', previewSlide);
                    }, 350);

                    e = window.event || e;

                    var index = that.currentPreviewJpg.parents('td').index();
                    var jpg;

                    //  get img
                    if (e.deltaY > 0) {
                        //  previous page
                        if (index > 0) {
                            jpg =  that.currentPreviewJpg.parents('td').prev('td').find('.icon-jpg');
                            that.currentPreviewJpg = jpg;
                            $('.task-preview img').prop('src', jpg.attr('data-url'));
                        }
                    } else {
                        //  next page
                        if (index < that.currentPreviewJpg.parents('tr').find('td').length-1) {
                            jpg = that.currentPreviewJpg.parents('td').next('td').find('.icon-jpg');
                            that.currentPreviewJpg = jpg;
                            $('.task-preview img').prop('src', jpg.attr('data-url'));
                        }
                    }
                }
            }
        },

        mainDatePicker: {
            updatePosition: function () {
                //  init datepicker position
                autoBindUpdateDatePickerPosition();

                //  update datepicker position
                $(window).on('scroll', updateDatePickerPosition);

                $(window).on('resize', autoBindUpdateDatePickerPosition);

                function updateDatePickerPosition () {
                    var windowWidth = $(window).outerWidth();
                    var bodyScrollLeft = $('body').scrollLeft();

                    $('.calendar-hd .datepicker').css({
                        'position': 'absolute',
                        'left': windowWidth + bodyScrollLeft - 188,
                        'float': 'none'
                    });

                    $('.calendar-hd .btn').css({
                        'position': 'absolute',
                        'left': windowWidth + bodyScrollLeft - 188 - 220,
                        'float': 'none'
                    });
                }

                function autoBindUpdateDatePickerPosition () {
                    if ($(window).outerWidth() <= 1723+38) {
                        updateDatePickerPosition();
                        $(window).on('scroll', updateDatePickerPosition);
                    } else {
                        $(window).off('scroll', updateDatePickerPosition);
                        $('.calendar-hd .datepicker, .calendar-hd .btn').css({
                            'position': 'inherit',
                            'left': 0,
                            'float': 'right'
                        });
                    }
                }
            },

            init: function () {
                this.updatePosition()
            }
        },

        init: function () {
            this.previewJPG.init();
            this.mainDatePicker.init();

            //  click task title to show task summary
            $('.calendar .task-hd').click(function (e) {
                e.stopPropagation();
                components.floatPanel.show($('.task-detail'));
            });
        }
    },

    toggleSidebar: {
        init: function () {
            $('.calendar-btn').click(function (e) {
                e.stopPropagation();
                $('.calendar-sidebar').toggleClass('active');
            });

            window.cc = function () {
                localStorage.removeItem('sisosystem');
            };

            //  auto open sidebar
            (function () {
                var cache;
                var curTime = new Date().getTime();

                if (localStorage.sisosystem && localStorage.sisosystem != 'null') {
                    cache = JSON.parse(localStorage.sisosystem);
                } else {
                    localStorage.sisosystem = JSON.stringify({
                        lastOpendDate: (new Date()).getTime(),
                        isOpened: false
                    });

                    cache = JSON.parse(localStorage.sisosystem);
                }

                if (cache) {
                    //  if opened sidebar more than 2 hours, auto reopen sidebar
                    if ((curTime - cache.lastOpendDate)/1000/60 >= 20) {
                        cache.isOpened = false;
                    }

                    if (cache.isOpened == false) {
                        cache.isOpened = true;
                        cache.lastOpendDate = (new Date()).getTime();

                        setTimeout(function () {
                            $('.calendar-sidebar').addClass('active');
                        }, 600);
                    } else {
                        $('.calendar-sidebar').removeClass('active');
                    }
                }

                // update localStorage
                localStorage.sisosystem = JSON.stringify(cache);
            })();
        }
    },

    init: function () {
        this.toggleTask.init();
        this.toggleSidebar.init();

        components.toggle('.task-editor .more', '.task-editor .desc');

        //  create task
        $('.calendar-editor .calendar-hd .create').click(function (e) {
            e.stopPropagation();
            $('.task-editor').removeClass('task-editor-editing').find('h2').text('');
            components.floatPanel.show($('.task-editor'));
        });

        //  edit task
        $('.calendar-editor .task-hd .icon-edite').click(function (e) {
            e.stopPropagation();
            $('.task-editor').addClass('task-editor-editing').find('h2').text('');
            components.floatPanel.show($('.task-editor'));
        });

        //  reset task
        $('.btn-reset').click(function () {
            $(this).parents('.m-float-panel').find('input, textarea').each(function () {
                $(this).val('');
            });
        });

        //  confirm task
        $('.btn-confirm').click(function () {
            components.floatPanel.hide($('.task-editor'));
        });

        //  set current date
        //(function () {
        //    var time = new Date().getDate() - 1;
        //    var precount = 0;
        //
        //    //  get calendar-item which date is before 1th
        //    $('.calendar-item').each(function () {
        //        var date = parseInt($(this).find('.bd').text());
        //
        //        if (date == 1) {
        //            return false;
        //        } else {
        //            precount += 1;
        //        }
        //    });
        //
        //    time += precount;
        //    $('.calendar-item').eq(time).addClass('active');
        //})();
    }
};

